<template>
  <div class="moreapp">
    <div class="topTxt">
    <div class="imgBox" @click="back">
    <img src="../../assets/img/black-left.png" alt="">
    </div>
    推荐更多锁粉APP
  </div>
    <div class="appname">
      <p class="apptip">APP名称<span></span></p>
      <input type="text" v-model="appData.app_name" :placeholder="appData.app_name ? '' : '填写你要推荐的APP名称'" maxlength="10">
    </div>
    <div class="react"></div>
    <div class="applogo">
      <p class="applogotip">添加APP图标 （选填）</p>
       <div class="img-list">
        <div class="iconSize" v-for="(item, index) in previewImgList" :key="index">
          <div class="imgWrapper">
            <img :src="item" preview="1" alt="图片">
          </div>
          <div class="deleteIcon" @click="deleteImage(index)"></div>
        </div>
        <div class="upload" v-if="previewImgList.length < 3">
          <div class="photo">
            <img src="../../assets/img/personal/photo.png" alt="上传">
          </div>
          <div class="tips">{{previewImgList.length > 0 ? `${previewImgList.length} / 3` : '添加图片'}}</div>
          <label class="labelContent" for="inputFile">
            <input class="uploadFile" id="inputFile" @change="uploadFile" multiple="multiple" accept="image/*" type="file">
          </label>
        </div>
      </div>
    </div>
    <div class="react"></div>
    <div class="tuijianreason">
      <p>推荐理由（选填）</p>
      <textarea name="" id="" v-model="appData.remark" cols="30" rows="10" maxlength="100"></textarea>
    </div>
    <div class="sumbit" @click="submitBtn">提交</div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      exampleurl: '',
      previewImgList: [],     // 预览图片对象
      fileList: [],
      appData: {}
    }
  },
  methods: {
    deleteImage (index) {
      this.fileList.splice(index, 1)
      this.previewImgList.splice(index, 1)
    },
    back () {
      this.$router.go(-1)
    },
    uploadFile (e) {
      if (!this.$utils.isImg(e)) {
        this.$toast({
          message: '请上传图片',
          position: 'middle',
          duration: 1500
        })
        return
      }
      if (e.target.files) {
        this.fileList = [e.target.files[0]]
        this.previewImgList = [...this.previewImgList, ...this.$utils.getPreviewList(e.target.files)].splice(0, 3)
        this.$previewRefresh()
      }
    },
    submitBtn () {
      if (!this.appData.app_name) {
        return this.$toast('请输入app名称')
      }
      this.$indicator.open({
        text: '提交中，请稍后'
      })
      setTimeout(() => {
        this.$indicator.close()
      }, 3000)
      this.$utils.uploaderMuti(this.$utils.cache('user_token'), 'app_recommend', this.fileList).then(imgList => {
        this.$indicator.close()
        let data = {
          app_name: this.appData.app_name,
          img_url: imgList.join(';'),
          remark: this.appData.remark ? this.appData.remark : ''
        }
        this.$api.post('SUBMIT_APP_RECOMMEND', data, res => {
          this.details = {
            audit_status: 1,
            content: data.content,
            picture_urls: this.previewImgList
          }
          this.$previewRefresh()
          this.showFlag = false
          this.$toast({
            message: '提交成功',
            duration: 1500
          })
          this.$router.go(-1)
          this.fileList = []
          this.previewImgList = []
          this.appData = []
        })
      }).catch(() => {
        this.$toast('提交失败')
        this.$indicator.close()
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.moreapp {
  width: 750px;
  height: 100%;
  .upload {
    width: 133px;
    height: 133px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    border: 1px dashed #999;
    position: relative;
    label {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
    }
    .photo {
      width: 49px;
      height: 43px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .tips {
      font-size: 24px;
      margin-top: 20px;
      color: #999;
    }
    input[type=file] {
      // display: none;
      width: 0;
      height: 0;
      overflow: hidden;
      visibility: hidden;
    }
  }
  .topTxt{
    line-height: 90px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #eee;
    text-align: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    .imgBox{
      position: absolute;
      width: 20px;
      top: 50%;
      transform: translateY(-50%);
      padding: 10px;
      left: 20px;
      height: 35px;
      line-height: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .right{
      position: absolute;
      right: 30px;
      top: 0;
      height: 90px;
    }
  }

  .appname {
    margin-top: 130px;
    padding-left: 30px;
    .apptip {
      span {
        width: 15px;
        height: 15px;
        display: inline-block;
        background: url("../../assets/img/lockfan/star.png") no-repeat center center;
        background-size: 15px 15px;
        vertical-align: top;
      }
    }
    input {
      width: 450px;
      height: 80px;
      border: 1px solid #999999 ;
      font-size: 30px;
      border-radius: 6px;
      padding-left: 22px;
      margin-top: 30px;
    }
  }

  .react {
    width: 750px;
    height: 20px;
    background: #F5F5F5;
    margin-top: 30px;
  }
  .applogo {
    margin-top: 40px;
    padding-left: 30px;
    .jietubox {
      width: 133px;
      height: 133px;
      display: inline-block;
      border: 1px dashed #999999;
      vertical-align: top;
      margin-top: 30px;
      margin-left: 26px;
    }
     .img-list {
      padding: 0 0 0 0;
      display: flex;
      flex-flow: nowrap;
      margin-top: 40px;
      .iconSize {
        background: #D8D8D8;
        width: 133px;
        height: 133px;
        margin-right: 30px;
        position: relative;
        .deleteIcon {
          width: 40px;
          height: 40px;
          background:  url('../../assets/img/personal/close.png') no-repeat center;
          background-size: 100%;
          right: -20px;
          top: -20px;
          position: absolute;
          cursor: pointer;
        }
        .imgWrapper {
          position: relative;
          width: 100%;
          height: 100%;
          overflow: hidden;
          >img {
            width: 100%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            height: auto;
          }
        }
      }
    }
  }
  .tuijianreason {
    margin-top: 40px;
    padding-left: 30px;
    textarea {
      width: 658px;
      height: 200px;
      margin-top: 20px;
      font-size: 28px;
      color: #666;
      line-height: 40px;
      padding-left: 22px;
      padding-top: 12px;
    }
  }

  .sumbit {
    width: 470px;
    height: 80px;
    margin:0 auto;
    margin-top: 168px;
    margin-bottom: 110px;
    text-align: center;
    background: #F37F32;
    color:#fff;
    border-radius: 40px;
    line-height: 80px;
  }

  .labelContent {

  }
}
</style>



