      </div>
      <div class="houseLoct">
        <div class="typeS f24">{{defaltArr[defaltArrIndex]}} {{newIndex}}/{{contNum.length}}</div>
        <div class=" f24"><span v-for="(item,index) in img_list_group" :class="defaltArr[defaltArrIndex]===index?'active':''" :key="index" @click="toggleText(index)">{{index}} ({{item.length}})</span></div>
      </div>
    </div>
    <ad-image :adObj="adObj5" v-if="adObj5 && showAd5"></ad-image>
