<script>
import HeaderNav from '@/components/task/header-nav'
import recommendButtom from '@/components/common/recommend_buttom'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import Swiper from 'swiper'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import adView from '@/components/mutiAdList/index'
import AdImage from '@/components/mutiAdList/float'

export default {
  components: {
    swiper,
    swiperSlide,
    HeaderNav,
    recommendButtom,
    IndexShare,
    ShareCommon,
    adView,
    AdImage
  },
  data () {
    return {
      setBrowser: {},
      adObj1: null,
      adObj2: null,
      adObj3: null,
      adObj4: null,
      adObj5: null,
      showAd1: true,
      showAd2: true,
      showAd3: true,
      showAd4: true,
      showAd5: true,
      showDetailValue: 1,
      listDate: {},
      swiperOptions: {
        slidesPerView: 1,
        spaceBetween: 30,
        observer: true,
        observeParents: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        initialSlide: 0
      },
      sildeIgm: {},
      common: [],
      floor_plan: [],
      img_list_group: {},
      contNum: [],          // 当前图片data
      defaltArr: [],        //  图片模块tabText
      defaltArrIndex: 0,  // 图片模块tab 下标
      newIndex: 1,        // 当前模块下图片下标
      sumIndex: 1,        // 总数图片下标
      threeLine: true,
      appShare: {
        count: 0
      },
      lastPageS: 0,
      newPage: 1,
      autoSw: false,
      showShare: false,
      titleHH: '没有更多啦',
      loopList: [] // 滚动数据
    }
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.setHeader)
  },
  methods: {
    deleteAd (index) {
      this.common.splice(index, 1)
    },
    closeAd (index) {
      this[`showAd${index}`] = false
    },
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      if (!id) {
        return
      }
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.houseDetail.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    setHeader () {
      this.scrollFn()
      if (this.$utils.getScrollTop() > document.querySelector('.slide-car').offsetHeight) {
        this.showDetailValue = 0
      } else {
        this.showDetailValue = 1
      }
      // console.log(this.$utils.getScrollTop())
      // console.log(document.querySelector('.slide-car').offsetHeight)
    },
    // 点击头部分享按钮（app分享）
    headerShare () {
      this.appShare.count++
    },
    // 关闭微信分享引导弹窗
    closeShare () {
      this.showShare = false
    },
    // 点击底部分享按钮
    setShare () {
      if (this.$config.source_style >= 6) {
        this.headerShare()
      } else if (this.$utils.isWx()) {
        this.showShare = true
      } else {
        this.$router.push({
          path: '/saveimage',
          query: {
            tag: 'goods'
          }
        })
      }
    },
    // 分享成功操作函数
    shareSuccess () {

    },
    // 在app打开
    openInApp () {
      if (this.$config.source_style < 6) {
        if (this.$utils.isWx()) {
          if (this.$utils.isIos()) {
            window.open(this.$config.iOSUrl)
          } else if (this.$utils.isAndroid()) {
            window.open(this.$config.applicationUrl)
          }
        }
      }
    },
    // 获取顶部滚动消息
    getTopList () {
      if (this.loopList.length === 0) {
        this.$api.get('GOODS_RANDOMTOP', {goods_id: this.$route.params.id}, res => {
          this.loopList = res.data
        })
      }
    },
    // 初始化数据
    initData () {
      // 重置数据
      this.setBrowser = {}
      this.showAd5 = false
      this.showAd4 = false
      this.showAd3 = false
      this.showAd2 = false
      this.showAd1 = false
      this.$nextTick(() => {
        this.showAd5 = true
        this.showAd4 = true
        this.showAd3 = true
        this.showAd2 = true
        this.showAd1 = true
      })
      this.$api.get('GET_REWARD_ALERT', {}, res => {
        if (res.data.ad_info) {
          // this.adObj5 = res.data.ad_info[0]
          console.log(res.data.ad_info)
        }
      })
      this.$api.get('HOUSE_DETAIL', {id: this.$route.params.id}, res => {
        this.adObj1 = res.data.ad_info[0]
        this.adObj2 = res.data.ad_info[1]
        this.adObj3 = res.data.ad_info[2]
        this.getComment()
        this.listDate = res.data
        this.sildeIgm = res.data.img_url
        this.floor_plan = res.data.floor_plan
        this.img_list_group = res.data.img_list_group
        let flag = true
        this.defaltArr = []
        for (let s in res.data.img_list_group) {
          this.defaltArr.push(s)
          if (flag) {
            this.defaultKey = s
            flag = false
          }
        }
        this.contNum = res.data.img_list_group[this.defaltArr[0]]
        // this.initSWnoe()
        this.getTopList()
        this.$previewRefresh()
      })
    },
    initSWnoe () {
      // this.$nextTick(() => {
      //   this.mySwiper = new Swiper('.slide-car', {
      //     slidesPerView: 1,
      //     spaceBetween: 30,
      //     observer: true,
      //     observeParents: true,
      //     initialSlide: 0
      //   })
      // })
    },
    initTypeHouse () {
      this.$nextTick(() => {
        this.mySwiper = new Swiper('.house-type', {
          slidesPerView: 1,
          spaceBetween: 30,
          autoplay: true,
          observer: true,
          observeParents: true,
          initialSlide: 0
        })
      })
    },
    touchSlide () {
      this.autoSw = true
      let that = this
      this.$nextTick(() => {
        this.mySwiper = new Swiper('.autoplay', {
          spaceBetween: 30,
          observer: true,
          observeParents: true,
          on: {
            slideChangeTransitionEnd: function (event) { // 从上一个过渡到下一个结束时
              if (this.realIndex === that.contNum.length - 1) {
                that.defaltArrIndex++
                if (that.defaltArrIndex > that.defaltArr.length - 1) {
                  that.defaltArrIndex = 0
                  that.newIndex = 1
                }
                that.contNum = that.img_list_group[that.defaltArr[that.defaltArrIndex]]  // 更新数组 设置回第一张
                this.slideTo(0, false)
              }
            },
            slideChangeTransitionStart: function () { // 从当前slide过渡到下一个时  更新总数进度下标 更新当前图片下标
              that.sumIndex = that.sildeIgm.indexOf(that.img_list_group[that.defaltArr[that.defaltArrIndex]][this.realIndex]) + 1
              that.newIndex = this.realIndex + 1
            }
          }
        })
      })
    },
    toggleText (text) { // 更新图片模块下标 更新数组 设置第一张
      this.defaltArrIndex = this.defaltArr.indexOf(text)
      this.contNum = this.img_list_group[this.defaltArr[this.defaltArrIndex]]
      this.newIndex = 1
      this.touchSlide()
    },
    getComment () {
      this.$api.get('HOUSE_COMMENT_LIST', {house_id: this.$route.params.id, page: this.newPage}, res => {
        if (this.newPage === 1) {
          res.data.data.splice(1, 0, this.adObj3)
        }
        this.common = this.common.concat(res.data.data)
        this.lastPageS = res.data.last_page
      })
    },
    moreData () {
      if (this.threeLine) {
        if (this.newPage < this.lastPageS) {
          this.newPage++
          this.getComment()
        }
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.setHeader, {passive: false})
    this.initData()
  },
  filters: {
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + '万'
      } else {
        return num
      }
    }
  }
}
</script>

