        padding-left:30px ;
      }
      &>.flex{
        margin: 23px 31px;
        border-bottom: 1px solid #ddd;
        &.lastChild{
          margin-bottom: 0;
        }
        .icon{
          width: 65px;
          span{
            display: block;
            height: 65px;
            width: 65px;
            img{
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }
        }
        .textC{
          padding-left: 26px;
          line-height: 42px;
          flex: 1;
          .comment-content {
            padding-top: 10px;
            padding-bottom: 65px;
          }
          &>div{
            line-height: 40px;
            img{
              width: 30px;
              height: 30px;
              vertical-align: center;
              margin-right: 10px;
            }
          }
          &>p{
            line-height: 55px;
          }
        }
      }
    }
  }
  .gradColor{
    color: #989898;
  }
  .blurColor{
    background: #BBE0EE;
    color: #3499FF;
    padding: 5px 10px;
    margin-right: 20px;
  }
  .fontWeight{
    font-weight: 600;
  }
  .just{
    justify-content: space-between;
    &>div{
      flex: 1;
      height: 42px;
      line-height: 42px;
      margin-bottom: 21.5px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow : ellipsis;
    }
  }
  .margin30{
    margin: 30px 0;
  }
  .margin70{
    margin: 70px 0;
  }
  .margin-b5{
    margin-bottom: 50px;
  }
  .pb{
    position: fixed;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    z-index: 100;
    background: rgb(0,0,0);
    .title{
      position: absolute;
      top: 0;
      left: 0;
      height: 60px;
      line-height: 60px;
      width: 100%;
      box-sizing: border-box;
      background: rgba(0,0,0,0.6);
      color: white;
      z-index: 100;
      &>span:first-child{
        width: 60px;
        height: 60px;
        text-align: center;
        img{
          width: 50%;
          vertical-align: middle;
        }
      }
      .text-center{
        flex: 1;
      }
    }
    .swiper-container{
      top: 50%;
      transform: translateY(-50%);
      max-height: 500px;
       img{
         width: 100%;
       }
    }
    .typeS{
      margin-bottom: 100px;
    }
    .houseLoct{
      color: white;
      position: absolute;
      bottom: 20px;
      padding-left: 20px;
      span{
        display: inline-block;
        padding: 10px 10px;
        border-radius: 5px;
        margin-right: 20px;
        margin-bottom: 20px;
        &.active{
          background: #2aba65;
        }
      }
    }
  }
  .adObj {
    background: #fff;
    overflow: hidden;
    .rect {
      height: 10px;
      background: #f5f5f5;
      width: 750px;
      margin-left: -30px;
    }
  }
  .adObj1 {
    padding: 0 30px;
  }
  .image-ad {
    position: fixed;
    z-index: 2;
    bottom: 450px;
    right: 35px;
  }
</style>



