<template>
  <div class="lockfanpage">
    <div class="topTxt">
      <div class="imgBox" @click="back">
        <img src="../../assets/img/black-left.png" alt="">
      </div>
      手把手教你使用锁粉神器
    </div>
    <div class="scrollaward" >
      <div class="shareicon" :class="{isAndroid: $utils.isAndroid()}" v-if="swiperList.length > 0">
        <img src="../../assets/img/lockfan/shareinfo.png" alt="">
      </div>
      <!-- <swiper class="swiper-no-swiping" :options="swiperOptions" v-if="swiperList.length > 0"> -->
      <swiper class="swiperContent" :options="swiperOptions" v-if="swiperList.length > 0">
        <swiper-slide v-for="(item, index) in swiperList" :key="index">
          <p>{{$utils.splitStr(item.nickname, 7)}}成功锁粉{{item.invite_num}}人   赚得价值{{item.worth_invite | setPeople}}元</p>
        </swiper-slide>
      </swiper>
    </div>
    <!-- swiper -->
    <div class="appcontent" :class="{active: changeappheight}">
      <div class="header">
        <img src="../../assets/img/lockfan/app.png" alt="">
        <p class="apptip">请自行前往应用市场下载</p>
      </div>
      <div class="rankingbox">
        <div class="tipbox">
          <p>排名</p>
          <p class="appname_content">APP</p>
          <p class="tuijian">推荐人数</p>
        </div>
        <div class="detailapp" v-for="(item,index) in appdata" :key="index">
          <div class="ranknum">{{slideList[index]}}</div>
          <div class="app">
            <div class="app_icon iconSize">
              <img :src="item.logo_img" alt="">
            </div>
            <div class="appname_content">{{$utils.splitStr(item.app_name, 6)}}</div>
          </div>
          <div class="usenum">
            {{item.recommend_count}}人
          </div>
        </div>
      </div>
      <!-- 展开按钮 -->
      <div class="openrankbox" v-if="!changeappheight" @click="changeappheight = !changeappheight">
        <p class="openlogo">
          <img src="../../assets/img/lockfan/arrowanimate.gif" alt="">
        </p>
        <p class="allopen">展开全部</p>
      </div>
    </div>
    <div class="getstepbox">
      <img src="../../assets/img/lockfan/relaxthree.png" alt="">
    </div>
    <div class="firststepbox">
      <div class="firsttipimg">
        <img src="../../assets/img/lockfan/firsttitle.png" alt="">
      </div>
      <div class="firststepimg">
        <img src="../../assets/img/lockfan/first.png" alt="">
      </div>
    </div>
    <div class="secondstepbox">
      <div class="secondtipimg">
        <img src="../../assets/img/lockfan/secondtitle.png" alt="">
      </div>
      <div class="secondstepimg">
        <img src="../../assets/img/lockfan/secondimg.png" alt="">
      </div>
    </div>
    <div class="thirdstepbox">
      <div class="thirdtipimg">
        <img src="../../assets/img/lockfan/thirdtitle.png" alt="">
      </div>
      <div class="thirdstepimg">
        <img src="../../assets/img/lockfan/thirdimg.png" alt="">
      </div>
    </div>
    <div class="footerbox" @click="jumpmoreappBtn">
      <span>推荐更多锁粉APP</span>
    </div>
  </div>
</template>
<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOptions: {
        direction: 'vertical',
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        loop: true
      },
      changeappheight: false,
      appdata: [],
      swiperList: [],
      slideList: {
        0: '第一名',
        1: '第二名',
        2: '第三名',
        3: '第四名',
        4: '第五名',
        5: '第六名',
        6: '第七名',
        7: '第八名',
        8: '第九名',
        9: '第十名'
      }
    }
  },
  methods: {
    jumpmoreappBtn () {
      this.$router.push({name: 'MoreApp'})
    },
    getTopList () {
      let data = {

      }
      this.$api.get('CAROUSEL_RANKING', data, res => {
        this.swiperList = res.data.slice(0, 100)
      })
    },
    getLoopList () {
      let data = {

      }
      this.$api.get('RECOMMEND_APP_LIST', data, res => {
        this.appdata = res.data
      })
    },
    back () {
      this.$router.go(-1)
    }
  },
  created () {
    this.getTopList()
    this.getLoopList()
  }
}
</script>
<style lang="scss" scoped>
.lockfanpage {
  width: 750px;
  height: 100%;
  .swiperContent {
    margin: 0;
    height: 60px;
    overflow: hidden;
    line-height: 60px;
  }
  .topTxt{
    line-height: 90px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #eee;
    text-align: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    .imgBox{
      position: absolute;
      width: 20px;
      top: 50%;
      transform: translateY(-50%);
      padding: 10px;
      left: 20px;
      height: 35px;
      line-height: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .right{
      position: absolute;
      right: 30px;
      top: 0;
      height: 90px;
    }
  }
  
  .scrollaward {
    width: 100%;
    height: 60px;
    margin-top: 90px;
    background-size: 30px 30px;
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    background: #FFF1D5;
    justify-content: center;
    color:#B79338;
    .shareicon {
      width: 30px;
      height: 29px;
      display: flex;
      margin-right: 16px;
      margin-top: 5px;
      align-items: center;
      &.isAndroid {
        margin-top: -1px;
      }
      img {
        vertical-align: top;
        width: 100%;
        height: 100%;
      }
    }
    p {
      display: inline-block;
      font-size: 28px;
    }
  }

  .appcontent {
    width: 690px;
    height: 959px;
    overflow: hidden;
    background: #FEF4E9;
    position: relative;
    border-radius: 20px;
    margin: 0 auto;
    margin-top: 32px;
    padding-top: 22px;
    padding-bottom: 100px;
    .header {
      width: 469px;
      height: 104px;
      margin: 0 auto;
      img {
        width: 100%;
        height: 100%;
      }
    .apptip {
      font-size: 28px;
      color: #666666;
      text-align: center;
      margin-top: 10px;
      }
    }
    .rankingbox {
      width: 618px;
      margin: 0 auto;
      .tipbox {
        padding-left: 30px;
        display: flex;
        flex-direction:row;
        font-weight: 900;
        justify-content:space-between;
        text-align: center;
        height: 97px;
        line-height: 97px;
        margin-top: 40px;
        .appname_content {
          width: 300px;
          text-align: center;
          margin-left: 20px
        }
        .tuijian {
        }
      }
      .detailapp {
        display: flex;
        flex-direction:row;
        height: 140px;
        justify-content:space-between;
        text-align: center;
        border-top: 1px solid #E5E5E5;
        line-height: 140px;
        .ranknum {
          margin-left: 10px;
        }
        .usenum {
          font-size: 26px;
          width:140px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #333;
        }
        .app {
          height: 100%;
          width: 300px;
          display: flex;
          align-items: center;
          flex-flow:  column wrap;
          text-align: center;
          justify-content: center;
          .app_icon {
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
          }
          .appname_content {
            height: 40px;
            line-height: 40px;
            color: #666;
            font-size: 26px;
            margin-top: 4px;
          }
          img {
            height: 100%;
            width: 100%;
          }
        }
      }
      
    }
    &.active {
      height: auto;
      padding-bottom: 0;
    }
  }
  .openrankbox {
    text-align: center;
    font-size: 26px;
    color: #333333;
    position: absolute;
    background: #fef4e9;
    height: 100px;
    box-sizing: border-box;
    padding-top: 10px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    .openlogo {
      display: flex;
      width: 26px;
      height: 26px;
      justify-content: center;
      margin: 0 auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .allopen {
      margin-top: 10px;
    }
  }
  .getstepbox {
    width: 512px;
    height: 64px;
    margin: 0 auto;
    margin-top: 53px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .firststepbox,.secondstepbox,.thirdstepbox {
    margin-top: 40px;
    padding-left: 26px;
    .firsttipimg {
      width: 628px;
      height: 37px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .secondtipimg {
      width: 477px;
      height: 37px;
        img {
          width: 100%;
          height: 100%;
        }
    }
    .thirdtipimg {
      width: 665px;
      height: 70px;
        img {
          width: 100%;
          height: 100%;
        }
    }
    .firststepimg,.secondstepimg,.thirdstepimg {
      width: 690px;
      height: 482px;
      margin-top: 18px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .thirdstepimg {
      height: 342px;
    }
  }
  .footerbox {
    width: 480px;
    height: 90px;
    font-size: 30px;
    color: #FFFFFF;
    text-align: center;
    margin: 76px auto;
    line-height: 90px;
    background: #F37F32;
    border-radius: 45px;
      span {
        font-size: 30px;
      }
  }
}
</style>



